body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Intro {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.3);
}

.Intro h1 {
  text-align: center;
  font-size: 75px;
  font-weight: 650;
  margin-top: -25px;
  margin-bottom: 25px;
}

.Intro p {
  width: 75%;
  text-align: center;
  font-size: 30px;
  font-weight: 450;
  margin-top: 0px;
  overflow-wrap: normal;
}

.Intro svg {
  width: 50px;
  height: 50px;
  opacity: 0.5;
  position: absolute;
  bottom: 10px;
  -webkit-animation: bounce 2s infinite;
          animation: bounce 2s infinite;
}

@-webkit-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  60% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  60% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
}

.fade {
  opacity:0;
  transition: 1s all ease-out;
}

.Skills {
  background-color: white;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #282c34;
  text-shadow: 1px 1px rgba(55, 61, 72, 0.2);
}

.Skills h1 {
  font-size: 60px;
  font-weight: 600;
  margin-top: 140px;
  margin-bottom: -15px;
}

.Skills h2 {
  font-size: 45px;
  font-weight: 500;
  padding: 30px;
  margin-bottom: -40px;
}

.Skills p {
  font-size: 30px;
  font-weight: 450;
  padding: 30px;
  margin-top: 0px;
}  
.Projects {
  height: 85vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  background-color: white;
  padding-top: 100px;
}

.Projects h1 {
  text-align: center;
  font-size: 75px;
  font-weight: 650;
  margin: 20px;
  color: #120845;
}
.Experience {
  height: 85vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  color: #120845;
  padding-top: 100px;
}

.Experience h1 {
  text-align: center;
  font-size: 75px;
  font-weight: 650;
  margin: 20px;
}

.Experience h2 {
  font-size: 40px;
  font-weight: 500;
  margin: 0px;
}

.Experience p {
  font-size: 20px;
  font-weight: 400;
}

.Education {
  width: 50%;
  height: 200px;
  display: flex;
  flex-direction: column;
  grid-row-gap: 20px;
  row-gap: 20px;
}

.University {
  display: flex;
  flex-direction: row;
  text-align: center;
}

.University .Info {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.University .Description {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.University .Description p {
    width: 80%;
    height: 100%;
    margin-top: -10%;
    overflow-wrap: normal;
}

.Queens {
    width: 50%;
    height: 100%;
    margin-top: -10%;
}

.HighSchool {
  display: flex;
  flex-direction: row;
  text-align: center;
}

.HighSchool .Info {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
  
.HighSchool .Description {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.HighSchool .Description p {
    width: 80%;
    height: 100%;
    margin-top: 6px;
    overflow-wrap: normal;
}

.ACVI {
    width: 30%;
    height: 60%;
}
.NavBar {
  width: 100%;
  height: 100px;
  background-color: #120746;
  background-image: linear-gradient(to top, #120746 60%, #150852);
  font-size: calc(10px + 4vmin);
  position: fixed;
  z-index: 1;
}

.Headers {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column-gap: 8%;
  -webkit-column-gap: 8%;
          column-gap: 8%;
}

.Headers a {
  text-decoration: none;
  color: white;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.3);
  font-weight: 500;
  margin-top: 20px;
  transition: color 0.2s ease-out, 0.2s;
}

.Headers a:hover {
  color: #E4E3E3;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.Footer {
  width: 100%;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Links svg {
  color: white;
  margin: 20px;
  margin-top: 0px;
  margin-bottom: 10px;
  font-size: 90px;
  cursor: pointer;
  transition: color 0.3s ease-out, 0.3s;
}

.Links svg:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  color: #E4E3E3;
}

@media only screen and (max-width: 800px) {
  .footer svg {
    font-size: 70px;
  }

  .footer {
    font-size: 20px;
    min-height: 150px;
    margin-top: 150px;
  }
}

@media only screen and (max-width: 600px) {
  .footer svg {
    font-size: 50px;
  }

  .footer {
    font-size: 15px;
  }
}
.App {
    background-image: linear-gradient(to top, black 10%, #120746 85%);
    min-height: 100vh;
    max-width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
